//Colors
$-blue: #2484FF;
$-dark:#141414;
$-dark-2: #414141;
$-dark-3: #383838;
$-dark-3-4: rgba(49, 53, 66, 0.4);
$-dark-3-2: rgba(49, 53, 66, 0.2);
$-dark-4: #7f828a;
$-gray-1: #888;
$-gray-2: #9ba5bb;
$-gray: #eeeeee;
$-red: #e05b74;
$-purple: #5628ee;
$-purple-2:  #877ff1;
$-yellow: yellow;
$gray: #9396A9;
$gray-2: #BBBBBB;
$gray-2-25: rgba(187, 187, 187, .25);
$gray-3: #DBDCDF;
$gray-4: #858993;
$gray-5: #979BA3;
$gray-6: #989898;
$white-1: #EEF1F7;
$dark: #282828;

$primary-color: #2A84FF;
$primary-border-color: #3571D8;
$primary-color-second: rgba(42, 132, 255, .2);
$primary-border-color-second: rgba(42, 132, 255, .55);
$secondary-color: #EEF1F7;
$secondary-text-color: #6F7377;
$hover-color: #f2f2f2;
$disabled-background-color: $gray-2-25; //$gray-3
$disabled-border-color: $gray-3; // $gray-2
$disabled-checked-background-color: $gray-4;
$disabled-checked-border-color: $gray-5;
$disabled-font-color: $gray-2;
$disabled-checked-font-color: $gray-6;
$outline-color: $primary-color-second;
$outline-border-color: $primary-border-color-second;

//Opacity
$disabled-opacity: .55;
$active-opacity: .55;

//Transition
$hover-transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
$transition-timing-function: cubic-bezier(0.64, 0.57, 0.67, 1.53);
$transition-duration-short: .25s;
$transition-duration-long: .4s;

//Shadow
$primary-shadow: 0 1px 3px rgba(41, 130, 255, .4);
$outline: 0 0 0 2px rgba(42, 132, 255, .25);
$second-shadow: 0 4px 14px rgba(0,0,0,.08);
$third-shadow: 0 1px 4px rgba(0,0,0,.20);
$extra-shadow: 0 7px 25px rgba(0,0,0,.25);

//Another

$-section-margin: calc(64px * 2);
$-section-width: calc(100% - #{$-section-margin});

$-default-font: -apple-system, BlinkMacSystemFont, 'Be Vietnam',system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif,
"Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
$-montserrat-font: 'Montserrat';