.form {
    padding: 32px;
    border: 1px solid #ced4da;
    border-radius: 8px;

    &.login {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        & h1 {
            font-size: 24px;
            margin: 0;
        }

        & .err {
            color: #2484FF;
            margin: 0;
            margin-top: 8px;
            margin-bottom: 16px;
            font-size: 13px;
        }

        & .textbox {
            margin-top: 16px;
        }

        & .btn {
            width: 100%;
            margin-top: 32px;
        }
    }
}

form {
  & .box {
    display: flex;
    width: 215px;
    margin: auto;
    margin-top: 24px;
    
    & button {
        margin: 0;
        min-width:unset;
        flex: 1;

        &:first-child {
            margin-right: 16px;
        }

        &:last-child {
            margin: 0;
        }
    }
 }
} 