//VIEWS
//#region
.full_size_view {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
}

.main_view {
    display: flex;
    background-color: $-dark;
}

//#endregion

//BODY, HTML
body,
html {
    font-family: $-default-font;
    margin: 0;
    padding: 0;
    overflow: hidden;

    & #root {
        > div {
            display: flex;

            & main {
                flex: 1;
                height: 100vh;
            }
        }
    }
}